<template>
    <div class="yingyong">
        <headerBar :title="headerNavData"></headerBar>
        <div class="code_tab">
          <yd-tab active-color="#278eed">
              <yd-tab-panel label="助记词">
                  <div class="gfcode">
                  <yd-cell-group title="" style="border:1px solid #ddd">
                      <yd-cell-item style="padding-left: .24rem!important">
                          <yd-textarea slot="right" placeholder="助记词，按空格分隔"></yd-textarea>
                      </yd-cell-item>
                  </yd-cell-group>

                  <yd-cell-group>
                      <yd-cell-item>
                          <span slot="left"></span>
                          <yd-input slot="right" v-model="codedata.name" :show-clear-icon="false" :show-success-icon="false"  placeholder="m/44 '/60' /0 '/0/0"></yd-input>
                      </yd-cell-item>
                      <yd-cell-item>
                          <span slot="left"></span>
                          <yd-input slot="right" type="password" v-model="codedata.password" :show-clear-icon="false" :show-success-icon="false" :show-required-icon="false" placeholder="密码"></yd-input>
                      </yd-cell-item>
                      <yd-cell-item>
                          <span slot="left"></span>
                          <yd-input slot="right" type="password" v-model="codedata.repassword" :show-clear-icon="false" :show-success-icon="false" :show-required-icon="false" placeholder="重复密码"></yd-input>
                      </yd-cell-item>
                      <yd-cell-item>
                          <span slot="left"></span>
                          <yd-input slot="right" type="password" v-model="codedata.showpassword" :show-clear-icon="false" :show-success-icon="false" :show-required-icon="false" placeholder="密码提示(可不填)"></yd-input>
                      </yd-cell-item>
                  </yd-cell-group>
                  <yd-flexbox class="reda">
                      <div class="chk">
                        <yd-checkbox-group v-model="codedata.check" color="#278eed" size="18">
                            <yd-checkbox val="1" shape="circle"></yd-checkbox>
                        </yd-checkbox-group>
                      </div>
                      <yd-flexbox-item>我已经仔细阅读并同意 <span>服务及隐私条款</span></yd-flexbox-item>
                  </yd-flexbox>
                  <button type="button" class="btn_c">开始导入</button>
                  <p class="loochk">什么是助记词?</p>
                </div>
              </yd-tab-panel>
              <!-- ----------------官方钱包------------------- -->
              <yd-tab-panel label="官方钱包">
                  <div class="gfcode">
                    <p class="lookcode_content">直接复制粘贴以太坊官方钱包keystore文件内容至输入框，或者通过生成keystore内容的二维码扫描录入</p>
                  <yd-cell-group title="" style="border:1px solid #ddd" class="mat3">
                      <yd-cell-item style="padding-left: .24rem!important">
                          <yd-textarea slot="right" placeholder="请输入keystore"></yd-textarea>
                      </yd-cell-item>
                  </yd-cell-group>

                  <yd-cell-group>
                      <yd-cell-item>
                          <span slot="left"></span>
                          <yd-input slot="right" type="password" v-model="codedata.password" :show-clear-icon="false" :show-success-icon="false" :show-required-icon="false" placeholder="keystore密码"></yd-input>
                      </yd-cell-item>
                  </yd-cell-group>
                  <yd-flexbox class="reda">
                      <div class="chk">
                        <yd-checkbox-group v-model="codedata.check" color="#278eed" size="18">
                            <yd-checkbox val="1" shape="circle"></yd-checkbox>
                        </yd-checkbox-group>
                      </div>
                      <yd-flexbox-item>我已经仔细阅读并同意 <span>服务及隐私条款</span></yd-flexbox-item>
                  </yd-flexbox>
                  <button type="button" class="btn_c">开始导入</button>
                  <p class="loochk">什么是keystore?</p>
                </div>
              </yd-tab-panel>
              <!-- ----------------私钥------------------- -->
              <yd-tab-panel label="私钥">
                <div class="skey">
                  <yd-cell-group title="" style="border:1px solid #ddd">
                      <yd-cell-item style="padding-left: .24rem!important">
                          <yd-textarea slot="right" placeholder="请输入明文私钥"></yd-textarea>
                      </yd-cell-item>
                  </yd-cell-group>

                  <yd-cell-group>
                      <yd-cell-item>
                          <span slot="left"></span>
                          <yd-input slot="right" type="password" v-model="codedata.password" :show-clear-icon="false" :show-success-icon="false" :show-required-icon="false" placeholder="密码"></yd-input>
                      </yd-cell-item>
                      <yd-cell-item>
                          <span slot="left"></span>
                          <yd-input slot="right" type="password" v-model="codedata.repassword" :show-clear-icon="false" :show-success-icon="false" :show-required-icon="false" placeholder="重复密码"></yd-input>
                      </yd-cell-item>
                      <yd-cell-item>
                          <span slot="left"></span>
                          <yd-input slot="right" type="password" v-model="codedata.showpassword" :show-clear-icon="false" :show-success-icon="false" :show-required-icon="false" placeholder="密码提示(可不填)"></yd-input>
                      </yd-cell-item>
                  </yd-cell-group>
                  <yd-flexbox class="reda">
                      <div class="chk">
                        <yd-checkbox-group v-model="codedata.check" color="#278eed" size="18">
                            <yd-checkbox val="1" shape="circle"></yd-checkbox>
                        </yd-checkbox-group>
                      </div>
                      <yd-flexbox-item>我已经仔细阅读并同意 <span>服务及隐私条款</span></yd-flexbox-item>
                  </yd-flexbox>
                  <button type="button" class="btn_c">开始导入</button>
                  <p class="loochk">什么是私钥?</p>
                </div>
              </yd-tab-panel>

              <!-- ----------------观察------------------- -->
              <yd-tab-panel label="观察">
                <div class="lookcode">
                  <p class="lookcode_content">观察钱包不需要导入私钥，只导入钱包地址，进行日常查看账户交易和接收通知。大额资金的私钥建议使用冷钱包或者硬件钱包管理，避免泄露被盗</p>
                  
                  <div class="code_input">
                    <input type="text" v-model="codeaddr" placeholder="扫码或者输入钱包地址">
                  </div>
                  <button type="button" class="code_btn">开始导入</button>
                  <p class="notice">注意：观察钱包发送交易时需要冷钱包配合签名，你可以使用另一台闲置手机，开飞行模式做冷钱包，导入钱包私钥，配合离线签名</p>
                  <p class="loochk">如何使用冷钱包进行离线签名?</p>
                </div>
              </yd-tab-panel>
          </yd-tab>
        </div>
    </div>
  
</template>

<script>
  import headerBar from './header.vue'
export default {
  components:{
      "headerBar":headerBar
  },
  data () {
    return {
      codedata:{
        name:'',
        password:'',
        repassword:'',
        showpassword:'',
        check:[1]
      },
      codeaddr:'',
      headerNavData:{
         left:true,
         title:'导入钱包',
         right:false,
         bj:'#2c94ed',
         search:false,
         share:false
      }
    }
  },
  methods:{
    
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.mat3{
  margin-top: .3rem;
}
.yingyong{
  padding-top: .9rem
}
/*官方钱包--------------------------------*/
.gfcode{
  padding: .2rem;
}
/*私钥------------------------------------*/
.skey{
  padding: .2rem;
}
.skey .yd-cell{
  border: 1px solid #f4f4f4!important;
}
.yd-cell-item{
  border-bottom: 1px solid #f4f4f4;
  padding-left: 0!important
}
.yd-checkbox-text{
  display: none;
}
.reda{
  padding: 0 0 0 .28rem;
  font-size: .26rem;
  color: #666;
}
.reda span{
  color: #278eed
}
.btn_c{
  margin: .35rem 0 .15rem;
  height: .7rem;
  line-height: .7rem;
  text-align: center;
  width: 100%;
  border: none;
  background: #cdcdcd;
  font-size: .28rem;
  color: #fff;
  border-radius: 5px;
}
.btn_c:active{
  background: #278eed;
}
/*观察-------------------------------------*/
.lookcode{
  padding: .2rem;
}
.lookcode_content{
  color: #257ac8;
  font-size: .28rem;
  text-align: justify;
}
.code_input{
  border-bottom: 1px solid #f4f4f4;
  width: 100%;
}
.code_input input{
   border: none;
   height: .8rem;
  line-height: .8rem;
  width: 100%;
  font-size: .3rem;
}
.code_btn{
  border: none;
  width: 100%;
  height: .7rem;
  line-height: .7rem;
  text-align: center;
  background: #278eed;
  color: #fff;
  font-size: .28rem;
  border-radius: 5px;
  margin: .35rem 0
}
.code_btn:active{
  background: #176fbf;
}
.notice{
  padding: .2rem;
  background: #f4f4f4;
  line-height: 1.5;
  font-size: .28rem;
  color: #333;
  text-align: justify;
  border-radius: 5px;
}
.loochk{
  text-align: center;
  color: #278eed;
  font-size: .28rem;
  margin-top: .3rem;
}
</style>



